*{
  margin: 0;
  padding: 0;
}
.div-0{
  width: 100%;
  min-height: 100%;
  display: flex;
}
.div-1{
  width: 50%;
  min-height: 1000px;
  background: linear-gradient(90deg,#FFD700,ghostwhite,white);
}
.div-2{
  width: 50%;
  min-height: 1000px;
background: linear-gradient(270deg,#FFD700,ghostwhite,white);
}
.div-3{
  width: 100%;
  min-height: 100px;
  background: linear-gradient(0deg,whitesmoke,#FFD700,ghostwhite);
  color: black;
  text-decoration: none;
  font-weight: 600;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  position: absolute;
  font-size: large;
  top: 20%;
} 
.div-3 a{
  background-color: tomato;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 20px;
}
.div-3 a:hover{
  background-color: rgb(255, 73, 41);
  box-shadow: 2px 2px 2px #005324;
}

.nav{
  background-color: #007514;
  color: #ffe600;
  min-height:80px;
  width: 100%;
  display: flex;
  gap: 10%;
  align-items: center;
  justify-content:center;
}
.nav a h2{
  background-color: #007332;
  color: #ffe600;
}
.nav a h2:hover{
  background-color: #005324;
  color: #fff59e;
  border-radius: 10px;
  padding: 5px;
}
a {
  color: inherit; 
  text-decoration: none;  
  cursor: pointer;
}
h1{
  background-color:#ffe600;
  color: #007332;
  text-align: center;
  width: 100%;
}
.form{
  width: 100%;
    height: 550px;
    display: flex;
    background-color: #ffe600;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
 form{
  background: black;
  color: white;
  border: 1px solid #ffe600;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  padding-top: 10px;
  width: 80%;
  height: 80%;
  padding-bottom: 50px;
  justify-content: flex-start;
}
table {
  background: black;
  color: white;
  width: 80%;
  height: 80%;
  max-width: 100%;
  max-height: 725px;
}
table tr{
  min-width: 730px;
}
table,
th,
td {
  border: 1px solid whitesmoke;
  padding: 5px;

}

th,
td {
  padding-right: 10px;
  padding-left: 10px;
  text-align: left;
  text-transform: capitalize;
}

th {
  background-color: rgb(20, 0, 0);
  color: rgb(255, 255, 228);
}

td {
  background-color: rgb(0, 61, 61);
  color: beige;
}
.image{
  max-width: 100px;
  overflow: scroll;
}
.table{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  background: #fffbea;
  min-height: 1000px;
}
.table-2{
  width: 100%;
  display: flex;
  overflow-x: auto;
  justify-content: center;
}

.update{
  color: #ffe600;
}
.update:hover{
  border-bottom: 1px solid #ffe600;
}
.cals{
  width: 100%;
  background: #000059;
  color: whitesmoke;
  min-height: auto;
  display: flex;
  position: fixed;
  bottom: 0px;
  align-items: center;
  flex-direction: column;
  padding-bottom: 3px;
}
button{
  padding: 5px;
  background-color: #ffe600;
  color: darkgreen;
  font-weight: 700;
  text-transform: capitalize;
  box-shadow: 2px 2px 2px #ff3d9e,
              -2px -2px 2px #69ffd2;
  border-radius: 5px;
  padding-left: 5px;
  padding-right: 5px;
  cursor: pointer;
  transition: all 0.5s ease;
}
button:hover{
  background-color: #ffef5b;
  color: rgb(0, 41, 0);
  box-shadow: -2px -2px 2px #ff3d9e,
              2px 2px 2px #69ffd2;
}
span{
  color: red;
  font-weight: 500;
}
.no-result{
  background:blanchedalmond;
  color: darkred;
  width: 100%;
  padding-top: 20px;
  text-align: center;
  padding-bottom: 10px;
  font-weight:600;
  min-height: 1000px;
  font-size: x-large;
}
.no-result div{
  padding-bottom: 20px;
  padding-top: 20px;
  
}
.no-result a{
  color:  #ffe600;
  background-color: black;
  box-shadow:2px 2px 2px black;
  padding: 5px;
  border-radius: 10px;
}
.no-result a:hover{
  color: #fff8b6;
  background-color: black;
  box-shadow: 2px 2px 2px #00ff6e, -2px -2px 2px rgb(111, 0, 255);
}
.back{
  background-color: grey;
  color: #ffe600;
  width: 100%;
  height: 50px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.back a:hover{
  color: #fff59e;
  border-bottom: 1px solid #000059;
}
input{
  border-radius: 10px;
  padding: 8px;
  font-weight: 1000;
  border: 1px solid white;
  cursor: text;
}
.show{
  border: transparent;
  margin-top: 30px;
  margin-bottom: 10px;
  transition: all 0.5s ease;
}
.show-text{
  width: 80%;
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffef5b;
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 5px;
  transition: all 1s ease;
}
.table-input-btn{
  display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 30px;
}
.table-input-btn button{
  width: 80px;
}
@media screen and (max-width:725px ) {
  .table-2{
    justify-content: flex-start;
  }
}
@media screen and (max-width:295px ) {
  .nav a h2 , h1{
    font-size: large;
  }
  form{
    width: 100%;
  }
  input{
    padding: 4px;
    width: 80%;
  }
}